import React, { useState } from "react";
import './MovieRow.css';
import { NavigateBefore, NavigateNext } from '@material-ui/icons';

// eslint-disable-next-line import/no-anonymous-default-export
export default ({title, tv, movies}) => {
    const [scrollX, setScrollX] = useState(0);

    const handleLeftArrow = () => {
        let x = scrollX + Math.round(window.innerWidth / 2);
        if (x > 0) {
            x = 0;
        }
        setScrollX(x);
    }

    const handleRightArrow = () => {
        let x = scrollX - Math.round(window.innerWidth / 2);
        let listW = movies.results.length * 150 * 2 || tv.results.length * 150
        if ((window.innerWidth - listW) > x) {
            x = (window.innerWidth - listW) - 60;
        }
        setScrollX(x);
    }

    return (
        <div className="movieRow">
            <h2>{title}</h2>
            <div className="movieRow--left" onClick={handleLeftArrow}>
                <NavigateBefore style={{fontSize: 50}} />
            </div>
            <div className="movieRow--right" onClick={handleRightArrow}>
                <NavigateNext style={{fontSize: 50}}/>
            </div>
            <div className="movieRow--listArea">
                <div className="movieRow--list" style={{
                    marginLeft: scrollX,
                    width: movies.results.length * 250 * 2 || tv.results.length * 250
                }}>
                    {tv.results.length > 0 && tv.results.map((item, key)=>(
                        <div key={key} className="movieRow--item">
                            <img src={`http://image.tmdb.org/t/p/w300${item.poster_path}`} alt={item.original_title}/>
                        </div>
                        
                    ))}
                    {movies.results.length > 0 && movies.results.map((item, key)=>(
                        <div key={key} className="movieRow--item">
                            <img src={`http://image.tmdb.org/t/p/w300${item.poster_path}`} alt={item.original_title} />
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}